<template>
  <div>
    <input v-model="model" @click="open()" type="text" class="form-control" :class="inputClass" readonly/>

    <div v-show="show" class="cascading-address">
      <ul>
        <li v-for="item in provinces" @click="setProvince(item.p)" :class="{'label label-success': p === item.p}">
          {{ item.p }}
        </li>
      </ul>

      <ul v-show="cities.length">
        <li v-for="item in cities" @click="setCity(item.n)" :class="{'label label-success': c === item.n}">
          {{ item.n }}
        </li>
      </ul>

      <ul v-show="areas.length">
        <li v-for="item in areas" @click="setArea(item.s)" :class="{'label label-success': a === item.s}">
          {{ item.s }}
        </li>
      </ul>

      <div v-show="p" class="form-group text-left">
        <label class="control-label text-success">
          <span class="text-muted">地址：</span>
          {{ p }} {{ c }} {{ a }}
        </label>
      </div>

      <div class="panel-footer text-right">
        <button @click="close()" class="btn btn-link btn-sm">关闭</button>
        <button @click="clear()" class="btn btn-default btn-sm">清空</button>
        <button @click="confirm()" class="btn btn-success btn-sm" :disabled="!(p&&c) && p!='国外'">确定</button>
      </div>
    </div>
  </div>
</template>
<style>
  .cascading-address {
    box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
    position: absolute;
    z-index: 99999;
    background: #fff;
    width: 318px;
  }

  .cascading-address ul {
    padding: 5px;
    border-bottom: 1px dotted #ddd;
    text-align: left;
  }

  .cascading-address ul li {
    font-size: 13px;
    list-style: none;
    display: inline-block;
    margin: 5px 7px;
    cursor: pointer;
  }

  .cascading-address .form-group {
    margin: 10px;
  }
</style>
<script lang="babel">
  import addressData from './cascadingAddressData.json'
  export default{
    props: {
      inputClass: String
    },
    data(){
      return {
        p: '',
        c: '',
        a: '',
        provinces: addressData,
        areas: '',
        cities: '',
        show: false
      }
    },
    computed: {
      model(){
        return this.p ? (this.p + ' ' + this.c + ' ' + this.a) : '';
      }
    },
    methods: {
      clear(){
        this.p = '';
        this.c = '';
        this.a = '';
        this.cities = [];
        this.areas = [];
      },
      open(){
        this.show = true;
      },
      close(){
        this.show = false;
      },
      confirm(){
        this.close();
        this.$emit('confirm', {
          province: this.p,
          city: this.c,
          area: this.a
        })
      },
      setProvince(p){
        this.p = p;
        this.c = '';
        this.a = '';
        this.areas = [];
        // 根据选中的 p(省份) 值更新 cities(城市列表)
        var result = this.provinces.filter(function (v) {
          return v.p === p;
        });
        this.cities = result[0].c || [];
      },
      setCity(c){
        this.c = c;
        this.a = '';
        var result = this.cities.filter(function (v) {
          return v.n === c;
        });
        this.areas = result[0].a || [];
      },
      setArea(a){
        this.a = a;
      }
    }
  }
</script>



